// import PropTypes from "prop-types";
import axios from "axios";
import React from "react";
import Movie from "../components/Movie";
import "./Home.css";

class Home extends React.Component {
  constructor(props) {
    super(props);
    console.log("Hello?");
    console.log(this);
  }
  state = {
    isLoading: true,
    movies: [],
  };

  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts.mx/api/v2/list_movies.json?rating=8.5&limit=10&sort_by=rating"
    );
    this.setState({ movies, isLoading: false });
    // const json = await (
    //   await fetch(
    //     `https://yts.mx/api/v2/list_movies.json?limit=10&sort_by=year`
    //   )
    // ).json();
    // // .catch((e) => {
    // //   console.log(
    // //     "There has been a problem with your fetch operation: " + e.message
    // //   );
    // // });
  };

  printMovie() {
    return;
  }

  componentDidMount() {
    // console.log(
    //   fetch(`https://yts.mx/api/v2/list_movies.json?limit=10&sort_by=year`)
    // );
    this.getMovies();
    // console.log(
    //   axios.get("https://yts.mx/api/v2/list_movies.json?limit=10&sort_by=year")
    // );
    console.log("Mounted!");
  }
  componentDidUpdate() {
    console.log("updated!");
  }
  componentWillUnmount() {
    console.log("unmounted!!!!!");
  }
  render() {
    console.log("rendered!");
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => {
              console.log(movie);
              return (
                <Movie
                  key={movie.id}
                  id={movie.id}
                  url={movie.url}
                  bgimg={movie.background_image}
                  coverimg={movie.large_cover_image}
                  genres={movie.genres}
                  title={movie.title}
                  summary={movie.summary}
                  description={movie.description_full}
                  year={movie.year}
                  rating={movie.rating}
                />
              );
            })}
          </div>
        )}
      </section>
    );
  }
}

export default Home;
